<template>
<div>
   
      <Headertop v-bind:posttitle="posttitle"></Headertop>
       <Tab></Tab>
       <h1>新闻页</h1>
      <Footer></Footer>
      
</div>
  
</template>
<script>

 import Headertop from "../components/header"
 import Footer from "../components/footer"
import Tab from "../components/tab"

export default {
     data(){
            return {
                posttitle:"曼地控股头部父组件传递给子组件的"
            }
        },
    components:{
        Headertop,
          Tab,
        Footer
    },
     methods:{
        run(){
            alert("我是父组件里的方法");
        }
    }

}
</script>
