import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/view/home.vue'
import About from '@/view/about.vue'
import News from '@/view/news.vue'
import Product from '@/view/porduct.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'home',
            redirect: '/Home'

        }, {
            path: '/home',
            name: ' Home',
            component: Home
        },
        {
            path: '/about',
            name: 'About',
            component: About
        },
        {
            path: '/news',
            name: 'News',
            component: News
        },
        {
            path: '/product',
            name: 'Product',
            component: Product
        }
    ]
})


