<template>
<div>
      <Headertop :headtext="headtext" :run="run"></Headertop>
       <Tab></Tab>
       <h1>产品页</h1>
      <Footer></Footer>  
</div>
  
</template>
<script>

 import Headertop from "../components/header"
 import Footer from "../components/footer"
   import Tab from "../components/tab"

export default {
    data(){
        return{
           headtext :"曼地头部产品父组件传递给子组件的"    
        }
    },
    components:{
        Headertop,
          Tab,
        Footer
    },
    methods:{
        run(){
            alert("我是父组件里的方法");
        }
    }


}
</script>






