<template>
        <div class="header-top">
            <slot>{{msg}}</slot>
             <slot :lstobj='lstobj'></slot>  
        </div>
</template>
<script>
export default {
        data(){
            return {
                msg:"曼地控股头部slot1",
                lstobj:{
                    name:"xiaoming"    
                }
            }
        }
}
</script>
<style>
.header-top{
    width: 100%;
    height:50px;
    line-height: 50px;
    background:#337ab7; 
    color: #fff;
}
</style>



