<template>
<div>
      <!-- <Headertop>
      </Headertop> -->
      <Sl>
          <slot>
           
          </slot>
      </Sl>
        
     
      <Banner></Banner>
        <Tab></Tab>
        <ul class="text_list">
                <li v-for="(items,index)  in text_list" 
                :class="{'active':isactive==index}"
                 @click="isactive=index">
                  {{items.text}}{{index}}
                  </li>
                  <div class="box">
                      <ul>
                          <li v-for="(ite,index)  in text_box" 
                            :class="{'cunter':boxactive==index}"
                             @click="boxactive=index">
                               {{ite.text}}--------------{{index}}
                          </li>
                      </ul>
                      
                  </div>
              
        </ul>
        <Footer></Footer>
</div>
  
</template>
<script>
// import Headertop from "../components/header";
import Footer from "../components/footer";
import Tab from "../components/tab";
import Banner from "../components/banner";
import Sl  from "../components/sl";

export default {
  data(){
    return {
        text_list:[
            { text: "什么是Vue.js？"},
            { text: "如果您想在深入了解Vue之前了解更多信息"},
            { text: "是构建用户界面的渐进框架。"},
            { text: "并且很容易与其他库或现有项目"},
            { text: "Vue也完全能够为复杂的单页应用程序"}
        ],
         text_box:[
            { text: "什么是Vue.js？box1"},
            { text: "如果您想在深入了解Vue之前了解更多信息 box2"},
            { text: "是构建用户界面的渐进框架。box3"},
            { text: "并且很容易与其他库或现有项目box4"},
            { text: "Vue也完全能够为复杂的单页应用程序box5"}
        ],
        isactive:0,
        boxactive:0
    }
  },
  components: {
    Tab,
    Banner,
    Footer,
    Sl
  },
  methods:{
      gets(isactive){
            console.log(this.isactive)
      }

    
  }
};
</script>
<style>
    .text_list>li{
        height: 45px;
        line-height: 45px;
        text-align: left;
        text-indent: 2em;
    }
    .active{
        color: red;
    }

    .cunter{
       color: hotpink;
    }
    .box{
        /* display: none; */
    }





</style>



