<template>
       <div class="tab_sum">
          	<ul>
			    <li v-for="item in navItem" >
                    <router-link :to="item.url">
                    {{item.text}}
                    </router-link>
                 </li>
		    </ul>
       </div>
</template>

<script>
export default {
  data() {
    return {
      navItem: [
        { text: "首页", url: "/home" },
        { text: "关于我们", url: "/about" },
        { text: "新闻", url: "/news" },
        { text: "产品", url: "/product" }
      ]
    };
  },
  mounted: {
   
  }
};
</script>

<style>
a {
  text-decoration: none;
  color: #000;
}

.tab_sum {
  width: 100%;
  height: 50px;
  line-height: 50px;
  background: #ccc;
}
.tab_sum > ul{
        margin: 0;
    padding: 0;
}
.tab_sum > ul > li {
  width: 25%;
  float: left;
  display: inline-block;
  height: 100%;
}
.tab_sum > ul > li>a{
    display: inline-block;
    width: 100%;
    height: 100%;

}

</style>



