<template>
        <div class="header-top">
            <h2>{{posttitle}}</h2>
            <h2>{{headtext}}</h2>
            <button @click='run()'>执行父组件传过来的方法</button>
        </div>
</template>
<script>
export default {
        data(){
            return {
                // msg:"曼地控股头部"
            }
        },
        props: ['posttitle','headtext',"run"]
        // 父子组件传递给子组件
}
</script>
<style>
.header-top{
    width: 100%;
    height:50px;
    line-height: 50px;
    background:#337ab7; 
    color: #fff;
}
</style>





